const invoices = [{
    _id : "68624565",item : "Amazon", qty : 10
  }];

export default {

    findAll(req, res, next) {
 res.json (invoices);
    },

    create(req, res) {
        const { item, qty, date} = req.body;
        if (!item) {
          return res.status(400).json({err : 'need item'});
        }
        if (!qty) {
            return res.status(400).json({err : 'need qty'});
          }
          if (date) {
            return res.status(400).json({err : 'need date'});
          }
          Invoice.create({ item, qty, date})
          .then(invoice => res.json(invoice))
          .catch(err => res.status(500).json(err));
      },

}

